.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html {
  scroll-behavior: smooth;
}

.bg{
  background-color:  #282c34 ;
  background-image:  url( './assets/IMG2.jpg' ) ;
  background-size: cover ;
  background-position:  center ;
  min-height: 100vh ;
  display:  flex ;
  flex-direction:  column ;
  justify-content:  left ;
  font-size:  calc(10px + 2vmin) ;
  color:  white ;
}

.nav{
  display: flex ;
  flex-direction:  row ;
  align-items:  flex-start ;
  justify-content:  left ;
  align-items:  center ;
  margin-top: 20px 
}


.logo{
  order: 1 ;
  flex:2;
  padding: 20px ;
  padding-left: 100px ;
  text-align: left ;
  margin: 0 ;
  font-size: calc(1em + 2vh);
}
.navTabs{
  order: 2 ;
  flex:1;
  justify-content: space-around ;
  display: flex ;
  flex-direction: row ;
  text-align: center ;
  padding: 20px ;
  font-size: calc(.08em + 2vh);
}
.navTab{
  padding: 0 ;
  margin: 0 ;
  color: #fff;
}
.navTab:hover{
  border: none;
  color: #fff;
  text-decoration: none;
}
.slogan{
  /* // display: flex ; */
  text-align: center ;
  /* padding: 20vh ; */
  padding-top: 20vh ;
  font-weight: 700 ;
  font-size: 4vh 
}
.intro{
  display: flex ;
  flex-direction: column ;
  background-color:  #c8c8d1 ;
  align-items: center ;
  justify-content: center ;
  padding-top: 10vh ;
  padding-bottom: 10vh ;
  padding-left: 10vw ;
  padding-right: 10vw ;
  color: #1f355a 
}
.introMedia{
  /* min-height: 20vh; */
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-around;
}
.introMedia div{
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-top: 10px;
}
.introMedia video{
  width: 320px;
  height: 240px;
  background-color: cadetblue;
  border-radius: 8px;
}
.introMedia img{
  width: 320px;
  height: 240px;
  background-color: cadetblue;
  border-radius: 8px;
}
.objective{
  display: flex ;
  flex-direction: column ;
  background-color:  #fff ;
  align-items: center ;
  justify-content: center ;
  padding-top: 10vh ;
  padding-bottom: 10vh ;
  padding-left: 10vw ;
  padding-right: 10vw ;
  color: #1f355a ;
}
.tools{
  display: flex ;
  flex-direction: column ;
  background-color:  #c8c8d1 ;
  align-items: center ;
  justify-content: center ;
  padding-top: 10vh ;
  padding-bottom: 10vh ;
  padding-left: 10vw ;
  padding-right: 10vw ;
  color: #1f355a ;
}

.support{
  display: flex ;
  flex-direction: column ;
  background-color:  #fff ;
  align-items: center ;
  justify-content: center ;
  padding-top: 10vh ;
  padding-bottom: 10vh ;
  padding-left: 10vw ;
  padding-right: 10vw ;
  color: #1f355a ;
}
.support img{
  width: 30%;
  margin: 40px 20px;
}

.tools > div {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.tools > div > div {
	flex-grow: 1;
	width: 45%;
	/* height: 100px; */
  margin: 2%;
  text-align: center;
  /* background-color: #fff; */
}
.tools img {
  width: 20%;
}
.tools h2{
  font-size: calc(1em + 1.2vh);
  /* font-weight: 500; */
}
.sectionsHead{
  font-size: calc(2em + 1vh);
  font-weight: bold;
  text-align: center;
  border-bottom: 2px solid #1f355a;
  padding-bottom: .2em;
  margin-bottom: .6em;
}
.paragraph{
  flex:4;
  align-self: center ;
  font-size: calc(1em + 1vh);
  text-align: center;
  word-break: normal ;
}
.teamPic{
  display: flex ;
  flex-direction: column ;
  background-color:  #fff ;
  align-items: center ;
  justify-content: center ;
  padding-top: 10vh ;
  padding-bottom: 10vh ;
  padding-left: 6vw ;
  padding-right: 6vw ;
  color: #fff ;
  background-image:  url( ./assets/team.jpg ) ;
  background-size: cover ;
  background-position:  center ;
  min-height: 50vh ;
  background-attachment: fixed ;
  text-shadow: 2px 2px 2px #000000a0 
}
.contacts{
  display: flex ;
  flex-direction: column ;
  background-color:  #fff ;
  align-items: center ;
  justify-content: center ;
  padding-top: 10vh ;
  padding-bottom: 10vh ;
  /* padding-left: 6vw ;
  padding-right: 6vw ; */
  color: #1f355a ;
}
.persons{
  display: flex ;
  flex-direction: row ;
  flex-wrap:  wrap ;
  align-items:  center ;
  justify-content:  center 
}
.person{
  margin: 40px ;
  padding: 20px ;
  background-color: #f5f5f5 ;
  border-radius: 5% ;
  display: flex ;
  flex-direction: column ;
  justify-content: center ;
  align-items: center 
}
.profile{
  border-radius: 50% ;
  width: 200px ;
  height: 200px ;
  margin-bottom: 20px 
}
.icons{
  display:  flex ;
  flex-direction:  row ;
  justify-content:  space-evenly ;
  width:  100% ;
}

@media only screen and (max-width: 1300px){
  .nav{
    display: flex ;
    flex-direction:  column ;
    align-items:  flex-start ;
    justify-content:  center ;
    align-items:  center ;
    margin-top: 20px 
  }
  .logo{
    order: 1 ;
    flex:1;
    padding: 20px ;
    /* padding-left: 100px ; */
    text-align: center ;
    margin: 0 ;
    font-size: calc(1em + 2vh);
  }
  .navTabs{
    order: 2 ;
    flex:1;
    width: 100%;
    justify-content: space-around ;
    display: flex ;
    flex-direction: row ;
    text-align: center ;
    padding: 20px ;
    font-size: calc(.08em + 2vh);
  }
  .paragraph{
    height: 300px;
    max-height: 300px;
    overflow-y: scroll;
    padding: 2px;
    background-color: rgba(199, 212, 202, 0.5);
    border-radius: 10px;
    word-break: normal;
  }
  .person{
    width: 33%;
    margin: 5px ;
    padding: 10px ;
    background-color: #f5f5f5 ;
    border-radius: 5% ;
    display: flex ;
    flex-direction: column ;
    justify-content: center ;
    align-items: center; 
    font-size: 10px;
    text-align: center;
  }
  .person h3{
    font-size: 15px;
    font-weight: bold;
    margin-bottom: 6px;
  }
  .person p{
    margin-bottom: 6px;
    
  }
  .profile{
    border-radius: 50% ;
    width: 100% ;
    height: 100% ;
    margin-bottom: 6px 
  }
  .intro{
    padding-left: 6vw ;
    padding-right: 6vw ;
  }
  .objective{
    padding-left: 6vw ;
    padding-right: 6vw ;
  }
  .contacts{
    padding-left: 6vw ;
    padding-right: 6vw ;
  }
  .teamPic{
    min-height: 35vh;
    background-attachment: inherit;
  }
  .introMedia{
    flex-direction: column;
    justify-content: center
  }
  .support img{
    width: 60%;
    margin: 40px 20px;
  }
}